<template>
  <div id="app">
    <c-header></c-header>
    <c-aside
      class="aside"
      :isCollapse.sync="isCollapse"
      :style="{width:`${asideWidth}px`}"
      @asideChange="asideWidthChange"
    />
    <c-main class="main" :asideWidth="asideWidth"/>
  </div>
</template>

<script>
import Aside from "@c/shell/Aside";
import Header from "@c/shell/Header";
import Main from "@c/shell/Main";
import { mapState, mapMutations, mapActions } from "vuex";
import { initSelectData } from "./select.js";

export default {
  name: "HelloWorld",
  data() {
    return {
      isCollapse: true,
      /* 与 婚检护士站通信的引用*/
      hjhszIframe: false
    };
  },
  created() {
    initSelectData();
  },
  mounted() {
    let vm = this;
    /* 获取侧边栏菜单 */
    vm.setMenuList();
    /* 初始化婚前医学检查模块的currentModel，一旦变化，该功能栏下所有模块数据都会相应变化*/
    /* 存储在localStorage的是基本类型字符串，需要使用JSON.parse转换后使用*/
    localStorage.setItem("fnbj.hjhsz.currentModel", "");
    /* 默认打开婚检花名册 */
    vm.addTab({
      id: "110201",
      name: "婚检花名册",
      title: "婚检花名册",
      icon: "search",
      url: "cx/hqbj_cx_jbzl.html",
      access: "True",
      isParent: "False",
      lb: "0",
      address: "婚前保健服务>综合查询>婚检花名册"
    });

    window.addEventListener("message", receiveMessage, false);
    function receiveMessage(event) {
      function takeAction({ data: { action, payload }, source, origin }) {
        console.log(action, payload, source, origin);
        const ACTIONS = {
          hjhszIframe() {
            vm.hjhszIframe = { source, origin };
          },
          openHjhsz() {
            /* 调用addTab方法打开 */
            vm.addTab({
              id: "110101",
              name: "婚检护士站",
              title: "婚检护士站",
              icon: "pencil-square",
              url: "yw/hqbj_hj_jbzl.html",
              access: "True",
              isParent: "False",
              lb: "0",
              address: "婚前保健服务>婚前医学检查>婚检护士站"
            });

            setTimeout(() => {
              vm.hjhszIframe.source.postMessage(
                {
                  action: "openHjhsz",
                  payload
                },
                vm.hjhszIframe.origin
              );
            }, 1000 * 1);
          }
        };
        ACTIONS[action] && ACTIONS[action](payload);
      }
      try {
        takeAction(event);
      } catch (error) {
        console.log(error);
      }
    }
  },
  methods: {
    ...mapActions(["setMenuList"]),
    ...mapMutations(["setAsideWidth", "addTab"]),
    asideWidthChange(rect) {
      console.log(rect);
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  computed: {
    ...mapState(["asideWidth"])
  },
  watch: {
    asideWidth(newValue, oldValue) {
      console.log("asideWidth", newValue, oldValue);
    },
    hjhszIframe(newValue) {
      console.log("hjhszIframe", newValue);
    }
  },
  components: {
    CHeader: Header,
    CAside: Aside,
    CMain: Main
  }
};
</script>
